"use client";

import {
  Box,
  Typography,
  Container,
  Button,
} from "@mui/material";
import AppleIcon from '@mui/icons-material/Apple';
import AndroidIcon from '@mui/icons-material/Android';

/* ======================
   MAIN SECTION
====================== */
const AppStore = () => {
  return (
    <Container maxWidth="lg" 
      sx={{ 
        py: 4,
        display: "flex", 
        flexDirection: { xs: "column", md: "row" },
        alignItems: "center",
        justifyContent: "space-between",
        backgroundColor: "#0b66f9",
        marginY: 8,
        gap: { xs: 4, md: 8 },
        px: { xs: 3, md: 6 },
      }}
    >

      {/* Left side - Text content */}
      <Box sx={{ 
        flex: 1,
        color: "white",
        textAlign: { xs: "center", md: "start" },
        position:"relative",
        left:"60px"

      }}>

     <Box
           
          
          >
        {/* Main heading */}
        <Typography
          variant="h3"
          fontWeight="700"
          sx={{
            mb: 2,
            fontSize: { xs: "12px", md: "16px", lg: "20px" },
            lineHeight: 1.2,
          }}
        >
          Get The App!
        </Typography>

        {/* Description */}
        <Typography
          sx={{
            mb: 4,
            fontSize: { xs: "10px", md: "14px" },
            lineHeight: 1.6,
            opacity: 0.9,
            maxWidth: "500px",
          }}
        >
          Our app has all your travel needs covered: Secure payment channels, easy 4-step booking process. What more could you ask for?
        </Typography>
       </Box>

        {/* Badges */}
        <Box sx={{ 
          display: "flex", 
          flexDirection: { xs: "column", sm: "row" },
          gap: 2, 
          justifyContent: { xs: "center", md: "flex-start" }
        }}>
          <Button
            variant="outlined"
            startIcon={<AndroidIcon />}
            sx={{
              color: "white",
              borderColor: "white",
              borderRadius: "8px",
              textTransform: "none",
              fontWeight: "600",
              fontSize: "10px",
              px: 2,
              py: 1,
              "&:hover": {
                backgroundColor: "black",
                borderColor: "white",
              }
            }}
          >
            PLAYSTORE
          </Button>
          
          <Button
            variant="outlined"
            startIcon={<AppleIcon />}
            sx={{ 
              color: "white",
              borderColor: "white",
              borderRadius: "8px",
              textTransform: "none",
              fontWeight: "600",
              fontSize: "10px",
              px: 2,
              py: 1,
              "&:hover": {
                backgroundColor: "black",
                borderColor: "white",
              }
            }}
          >
            APP STORE
          </Button>
        </Box>
      </Box>

      {/* Right side - Mobile Image */}
      <Box sx={{ 
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flex: 1,
      }}>
        <Box
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            width: "100%",
            maxWidth: "300px",
            position:"relative",
            bottom:-33
          }}
        >
          <img 
            src="/styles/mobileImg.png" 
            alt="Mobile App" 
            style={{
              width: "100%",
              height: "auto",
              maxHeight: "400px",
              objectFit: "contain",
            }}
          />
        </Box>
      </Box>
    </Container>
  );
};

export default AppStore;