"use client";

import { useState, useRef, useEffect } from "react";
import {
  MapPin,
  Users,
  Search,
  ChevronDown,
  Plus,
  Minus,
  X,
} from "lucide-react";
import {
  Grid,
  TextField,
  InputAdornment,
  IconButton,
  Button,
  Box,
  Typography,
  Paper,
  Popper,
  ClickAwayListener,
} from "@mui/material";
import { countriesAirports } from "../../data/countriesAirports";

const TourSearchForm = () => {
  // ------------------- HYDRATION FIX -------------------
  const [mounted, setMounted] = useState(false);
  useEffect(() => setMounted(true), []);

  // ------------------- CITY -------------------
  const [citySearch, setCitySearch] = useState("");
  const [selectedCity, setSelectedCity] = useState(null);
  const [cityAnchorEl, setCityAnchorEl] = useState(null);

  const allCities = countriesAirports.flatMap((country) =>
    country.cities.map((city) => ({
      city: city.city,
      country: country.country,
      code: city.code,
      airport: city.airport,
    }))
  );

  const filteredCities = allCities.filter(
    (item) =>
      item.city.toLowerCase().includes(citySearch.toLowerCase()) ||
      item.country.toLowerCase().includes(citySearch.toLowerCase())
  );

  const handleCityClick = (event) => {
    setCityAnchorEl(event.currentTarget);
  };

  const handleCityClose = () => {
    setCityAnchorEl(null);
  };

  const handleCitySelect = (city) => {
    setSelectedCity(city);
    setCitySearch(`${city.city}, ${city.country}`);
    handleCityClose();
  };

  // ------------------- DATES -------------------
  const [checkInDate, setCheckInDate] = useState("");
  const [checkOutDate, setCheckOutDate] = useState("");

  useEffect(() => {
    const today = new Date();
    const next = new Date(today);
    next.setDate(today.getDate() + 7);

    setCheckInDate(today.toISOString().split("T")[0]);
    setCheckOutDate(next.toISOString().split("T")[0]);
  }, []);

  // ------------------- TRAVELLERS -------------------
  const [travellers, setTravellers] = useState({
    adults: 1,
    children: 0,
    infants: 0,
  });

  const [travellerAnchorEl, setTravellerAnchorEl] = useState(null);

  const handleTravellerClick = (event) => {
    setTravellerAnchorEl(event.currentTarget);
  };

  const handleTravellerClose = () => {
    setTravellerAnchorEl(null);
  };

  const updateTraveller = (type, value) => {
    if (type === "adults" && value >= 1 && value <= 9) {
      setTravellers((prev) => ({ ...prev, [type]: value }));
    } else if (type === "children" && value >= 0 && value <= 8) {
      setTravellers((prev) => ({ ...prev, [type]: value }));
    } else if (
      type === "infants" &&
      value >= 0 &&
      value <= 5 &&
      value <= travellers.adults
    ) {
      setTravellers((prev) => ({ ...prev, [type]: value }));
    }
  };

  const totalTravellers = travellers.adults + travellers.children + travellers.infants;
  const travellersLabel = totalTravellers > 0 
    ? `${totalTravellers} Traveller${totalTravellers > 1 ? "s" : ""}`
    : "Travellers";

  if (!mounted) return null;

  return (
    <Box sx={{ width: "100%" }}>
      <Grid container spacing={2} alignItems="center" margin={2} paddingTop={2} paddingBottom={2}>
        {/* CITY */}
        <Grid item xs={12} sm={4}>
          <ClickAwayListener onClickAway={handleCityClose}>
            <Box>
              <TextField
                fullWidth
                size="small"
                placeholder="Search by City"
                value={selectedCity ? `${selectedCity.city}, ${selectedCity.country}` : citySearch}
                onChange={(e) => {
                  setCitySearch(e.target.value);
                  setSelectedCity(null);
                  if (!cityAnchorEl) {
                    setCityAnchorEl(e.currentTarget);
                  }
                }}
                onClick={handleCityClick}
                sx={{
                  "& .MuiInputBase-root": {
                    height: 40,
                    width: 280,
                    fontSize: "12px",
                  },
                }}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <MapPin size={16} />
                    </InputAdornment>
                  ),
                  endAdornment: (
                    <InputAdornment position="end">
                      <ChevronDown size={16} />
                    </InputAdornment>
                  ),
                }}
              />
              <Popper
                open={Boolean(cityAnchorEl)}
                anchorEl={cityAnchorEl}
                placement="bottom-start"
                disablePortal
                modifiers={[
                  {
                    name: "offset",
                    options: { offset: [0, 6] },
                  },
                ]}
                sx={{ zIndex: 1200 }}
              >
                <Paper
                  sx={{
                    maxHeight: 250,
                    overflowY: "auto",
                    width: 280,
                    boxShadow: 3,
                  }}
                >
                  {filteredCities.length > 0 ? (
                    filteredCities.map((city, index) => (
                      <Box
                        key={`${city.city}-${city.country}-${index}`}
                        onClick={() => handleCitySelect(city)}
                        sx={{
                          px: 2,
                          py: 1,
                          fontSize: "12px",
                          cursor: "pointer",
                          "&:hover": {
                            backgroundColor: "#f0f7ff",
                          },
                        }}
                      >
                        {city.city}, {city.country}
                      </Box>
                    ))
                  ) : (
                    <Box
                      sx={{
                        px: 2,
                        py: 1,
                        fontSize: "12px",
                        color: "text.secondary",
                      }}
                    >
                      No results found
                    </Box>
                  )}
                </Paper>
              </Popper>
            </Box>
          </ClickAwayListener>
        </Grid>

        {/* CHECK-IN */}
        <Grid item xs={12} sm={3} width={300}>
          <TextField
            fullWidth
            size="small"
            type="date"
            value={checkInDate}
            onChange={(e) => setCheckInDate(e.target.value)}
            sx={{
              "& .MuiInputBase-root": {
                height: 40,
              },
            }}
            InputProps={{
              sx: {
                fontSize: 12,
              },
            }}
          />
        </Grid>

        {/* TRAVELLERS */}
        <Grid item xs={12} sm={3}>
          <Button
            fullWidth
            variant="outlined"
            size="small"
            onClick={handleTravellerClick}
            sx={{
              justifyContent: "space-around",
              fontSize: 12,
              textTransform: "none",
              height: 40,
              width: 190,
              color: "#000000",
            }}
            endIcon={<Users size={16} />}
          >
            {travellersLabel.toLowerCase()}
          </Button>

          <Popper
            open={Boolean(travellerAnchorEl)}
            anchorEl={travellerAnchorEl}
            placement="bottom-start"
            disablePortal
            modifiers={[
              {
                name: "offset",
                options: { offset: [0, 6] },
              },
            ]}
            sx={{ zIndex: 1200 }}
          >
            <ClickAwayListener onClickAway={handleTravellerClose}>
              <Paper sx={{ p: 2, width: 220, borderRadius: 1 }}>
                {/* Header */}
                <Box display="flex" justifyContent="space-between" mb={1}>
                  <Typography sx={{ fontSize: 12, fontWeight: 600 }}>
                    Travellers
                  </Typography>
                  <IconButton
                    size="small"
                    onClick={handleTravellerClose}
                    sx={{ p: 0.5 }}
                  >
                    <X size={16} />
                  </IconButton>
                </Box>

                {/* Adults / Children / Infants */}
                {[
                  { key: "adults", label: "Adults", sub: "12+ years", max: 9, min: 1 },
                  { key: "children", label: "Children", sub: "2â€“11 years", max: 8, min: 0 },
                  { key: "infants", label: "Infants", sub: "Under 2", max: 5, min: 0 },
                ].map(({ key, label, sub, max, min }) => (
                  <Box
                    key={key}
                    display="flex"
                    justifyContent="space-between"
                    mb={1}
                  >
                    <Box>
                      <Typography sx={{ fontSize: 10 }}>{label}</Typography>
                      <Typography sx={{ fontSize: 8 }} color="text.secondary">
                        {sub}
                      </Typography>
                    </Box>
                    <Box display="flex" alignItems="center">
                      <IconButton
                        size="small"
                        disabled={travellers[key] <= min}
                        onClick={() =>
                          updateTraveller(key, travellers[key] - 1)
                        }
                        sx={{ p: 0.5 }}
                      >
                        <Minus size={16} />
                      </IconButton>
                      <Typography sx={{ mx: 1, fontSize: 10 }}>
                        {travellers[key]}
                      </Typography>
                      <IconButton
                        size="small"
                        disabled={
                          travellers[key] >= max ||
                          (key === "infants" &&
                            travellers.infants >= travellers.adults)
                        }
                        onClick={() =>
                          updateTraveller(key, travellers[key] + 1)
                        }
                        sx={{ p: 0.5 }}
                      >
                        <Plus size={16} />
                      </IconButton>
                    </Box>
                  </Box>
                ))}
              </Paper>
            </ClickAwayListener>
          </Popper>
        </Grid>

        {/* Search Button */}
        <Grid item xs={12} sm={2}>
          <IconButton
            color="primary"
            size="small"
            sx={{
              backgroundColor: "#1976d2",
              "&:hover": { backgroundColor: "#115293" },
              width: 40,
              height: 40,
              borderRadius: 1,
            }}
          >
            <Search style={{ color: "white" }} />
          </IconButton>
        </Grid>
      </Grid>
    </Box>
  );
};

export default TourSearchForm;